import React  from "react";
import * as P from "./parts"

interface HamburgerProps{
    clickCallback: () => void;
    className?: string;
    isOpen: boolean;
}
const Hamburger: React.FC<HamburgerProps> = ({clickCallback, className, isOpen}) => {

    return(
    <P.StyledWrapper className={className} onClick={clickCallback} >
        <P.StyledBar isOpen = {isOpen}/>
        <P.StyledBar isOpen = {isOpen}/>
        <P.StyledBar isOpen = {isOpen}/>
    </P.StyledWrapper>
)};
export default Hamburger
