import React from "react";
import * as P from "./parts"

interface NavigationProps{
    className?: string;
    onClickHandler: () => void;
}

const navbarItems = [
    {
        link: 'home',
        title: 'home',
        offset: 0,
    },
    {
        link: 'aboutMe',
        title: 'o mnie',
        offset: 0,
    },
    {
        link: 'lessons',
        title: 'zajęcia',
        offset: 0,
    },
    {
        link: 'opinions',
        title: 'opinie',
        offset: 0,
    },
    {
        link: 'contact',
        title: 'kontakt',
        offset: 0,
    },
]

const Navigation: React.FC<NavigationProps> = ({className, onClickHandler}) => (
    <P.StyledWrapper className={className} >
        {
            navbarItems.map( (item, key) => (
                <P.StyledItem key={key} to={`${item.link}`} smooth={true} offset={-70+item.offset} onClick={()=>{onClickHandler()}} >{item.title}</P.StyledItem>
            ))
        }
    </P.StyledWrapper>
);
export default Navigation
