import * as React from "react"
import * as P  from './parts';
import MainWrapper from 'components/MainWrapper';
import {useState} from "react";

interface HeaderProps {
    className?: string;
}

const Header: React.FC<HeaderProps> = ({ className }) => {

    const [isOpen, setOpen] = useState(true);
    const toggleNavbar = () => { isOpen ? setOpen(false) : setOpen(true);};

    return (
        <P.HeaderWrapper className={className}>
            <P.StyledHeader>
                <MainWrapper>
                    <P.StyledLogo/>
                    <P.HorizontalNavigation onClickHandler={()=>{}}/>
                    <P.StyledHamburger isOpen={isOpen} clickCallback={ toggleNavbar }/>
                </MainWrapper>
            </P.StyledHeader>
            <P.MobileNavigation onClickHandler={ toggleNavbar } isOpen={isOpen} />
        </P.HeaderWrapper>)
};
export default Header;
