import React, {Attributes, SyntheticEvent, useState} from "react";
import * as P from './parts'
import * as G from 'utils/grid'
import MainWrapper from "../../components/MainWrapper";

const emailPattern = /^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/;

const Contact: React.FC = () => {

    const [ isEmailValidate, setEmailValidate ] = useState( true );
    const [ isTextlValidate, setTextValidate ] = useState( true );

    const [ email, setEmail ] = useState( '' );
    const [ text, setText ] = useState( '' );

    const removeEmailError = ( email: string ) =>{
        if( !isEmailValidate && emailPattern.test(email) )
            setEmailValidate(true);
    };

    const removeTextError = ( text: string ) =>{
        if( text )
            setTextValidate(true);
    };

    const sendEmail = () => {

        const emailCondition = emailPattern.test(email);

        if(emailCondition)
            setEmailValidate(true);
        else
            setEmailValidate(false);

        if( text )
            setTextValidate( true );
        else
            setTextValidate(false);

        if(emailCondition && text) {
            alert("wiadomośc wysłana");
            setEmail("");
            setText("");
        }
    };

    return( <P.StyledWrapper>
        <MainWrapper>
            <G.Row>
                <G.Col sizes={[1, 1, 1 / 2, 1 / 2, 1 / 2]}>
                    <P.Header>Kontakt</P.Header>
                    <P.Describe>Nauczanie to sztuka, a skuteczny nauczyciel to prawdziwy skarb.
                        Skontaktuj się ze mną i zacznijmy wspólnie pracować nad Twoim sukcesem!</P.Describe>
                    <P.List>
                        <P.Item>Justyna Płonka</P.Item>
                        <P.Item>biuro@tutorium.edu.pl</P.Item>
                        <P.Item>{'+48 788 901 902'}</P.Item>
                    </P.List>
                </G.Col>
                <G.Col sizes={[1, 1, 1 / 2, 1 / 2, 1 / 2]}>
                    <P.FormWrapper>
                        <P.StyledLabel htmlFor="">Twój email</P.StyledLabel>
                        <P.GroupInput>
                            <P.StyledInput value={ email } isValidate={isEmailValidate} onChange={(e) => {
                                setEmail(e.target.value);
                                removeEmailError(e.target.value)
                            }} type={"text"}/>
                            <P.ErrorValidate  isValidate={isEmailValidate} >Email jest niepoprawny</P.ErrorValidate>
                        </P.GroupInput>
                        <P.StyledLabel  htmlFor="">Wiadomość</P.StyledLabel>
                        <P.GroupInput>
                            <P.StyledTextarea
                                isValidate={isTextlValidate}
                                onChange={(e) => {
                                    setText( e.target.value );
                                    removeTextError(e.target.value);
                                }}
                                value={ text }
                                name=""
                                id=""
                                cols={30}
                                rows={10} />
                            <P.ErrorValidate  isValidate={isTextlValidate} >Zadaj mi pytanie :)</P.ErrorValidate>
                        </P.GroupInput>
                        <P.StyledSubmit  onClick={ ()=>{ sendEmail() }}>Wyślij</P.StyledSubmit>
                    </P.FormWrapper>
                </G.Col>
            </G.Row>
        </MainWrapper>
    </P.StyledWrapper>
)}
export default Contact;
